import { cookies } from "next/headers";
import { NextRequest, NextResponse } from "next/server";
import { checkArcGISSession } from './app/lib/authenticateArcGIS'

export default async function middleware(req: NextRequest) {
 const checkSession = async () => {
      const session = await checkArcGISSession();

      if (session.loggedIn) {
            return NextResponse.redirect(process.env.NEXT_PUBLIC_GEOPORTAL_URL || '/');
      }
    };

    checkSession();

}